.hero {
    display: flex;
    flex-direction: column;
    width: 100%;
    padding: 36px;
}

.hero>img {
    margin: auto;
    align-self: center;
}

.hero>h1 {
    font-weight: 600;
    font-size: 36px;
    line-height: 110%;
    color: black;
}

.hero>p {
    margin-top: 0;
    width: 320px;
    font-weight: 300;
    font-size: 16px;
    line-height: 110%;
}