.card {
    position: relative;
    width: 176px;
    font-size: 12px;
    line-height: 20px;
    flex: 0 0 auto;         /* ADD */
    display: flex;          /* ADD */
    flex-direction: column; /* ADD */
}

.card>p {
    margin: 0;
}

.card-title {
    overflow: hidden;
    text-overflow: ellipsis;
}

.card-img {
    height: 235px;
    margin-bottom: 2px;
}

.card-badge {
    position: absolute;
    top: 6px;
    left: 6px;
    background-color: white;
    padding: 5px 7px;
    border-radius: 2px;
    font-weight: bold;
}

.card-star {
    width: 14px;
}

.card-rating-container {
    display: flex;
    align-items: center;
}

.card-rating {
    margin: 0 2px;
}

.card-rating-details {
    color: #918E9B;
}